import React, { Component } from 'react'
import axios from 'axios';
//import Table from 'react-bootstrap/Table'
import './CountryWise.scss'
// import CountryData from './CountryData'
import PreLoader from '../Loader/Loader'
import BackToTop from '../BackToTop/BackToTop';
import { MDBDataTable } from 'mdbreact';
import { NavLink } from 'react-router-dom'


const apiUrl = 'https://api.thevirustracker.com/free-api?countryTotals=ALL/'

class CountryWise extends Component {
    constructor(props) {
        super(props)
        this.state = {
             countries : [],
             isLoading : false
        }  
    }
    
    componentDidMount(){
        axios.get(apiUrl)
        .then(response =>{
            this.setState({
                countries : response.data.countryitems[0],
                isLoading : true
            })
        }).catch(error => {
            console.log(error)
        })
    }

    render() {
        const {countries} = this.state;
        // Get the values object from country object
        const getCountryData = Object.values(countries)

        //Remove the last item from object
        getCountryData.pop()

        // sort by country title
        getCountryData.sort(function(a, b) {
            var nameA = a.title.toUpperCase(); 
            var nameB = b.title.toUpperCase(); 
            if (nameA < nameB) {
            return -1;
            }else if (nameA > nameB) {
            return 1;
            }else{
                return 0;
            }
        });

        
        var rowsData = getCountryData.map(function(country){ 
            return {ourid:country.ourid, 
                    title:country.title,
                    total_cases: country.total_cases,
                    total_active_cases: country.total_active_cases,
                    total_recovered: country.total_recovered,
                    total_deaths: country.total_deaths,
                    total_serious_cases :country.total_serious_cases,
                    button : <NavLink className="get-btn" to={{
                        pathname : `/country/${country.ourid}`,
                        state : [{
                            id : country.ourid,
                            title : country.title,
                            code : country.code,
                            total : country.total_cases,
                            active : country.total_active_cases,
                            death : country.total_deaths,
                            recovered : country.total_recovered,
                            serious : country.total_serious_cases
                        }]
                        }} 
                    >Get Details</NavLink>
                    }; 
        }) 

        const tableData = {
            columns: [
            {
            label: '#',
            field: 'ourid',
            sort: 'asc',
            },
            {
            label: 'County',
            field: 'title',
            sort: 'asc',
            },
            {
            label: 'Total',
            field: 'total_cases',
            sort: 'asc',
            },
            {
            label: 'Active',
            field: 'total_active_cases',
            sort: 'asc',
            },
            {
            label: 'Recovered',
            field: 'total_recovered',
            sort: 'asc',
            },
            {
            label: 'Deaths',
            field: 'total_deaths',
            sort: 'asc',
            },
            {
            label: 'Serious Case',
            field: 'total_serious_cases',
            sort: 'asc',
            },
            {
            label: '',
            field: 'button',
            sort: 'asc',
            }
            ],
            rows: rowsData
            };

        return (
            <>
            {this.state.isLoading ? (<div className="browse-country"><div className="container datatableWrapper">
                <MDBDataTable className="text"  striped bordered hover data={tableData} />
                <BackToTop/>
            </div></div>): <PreLoader/> }
            
            {/* Code for normal table */}
            {/* {this.state.isLoading 
            ? (<div className="table-wrap">
                    <div className="container">
                        <Table responsive striped bordered hover variant="dark">
                            <thead>
                                <tr>
                                    <th>#</th>
                                    <th>Country </th>
                                    <th className="text-center skyblue">Total</th>
                                    <th className="text-center yellow">Active</th>
                                    <th className="text-center green">Recovered</th>
                                    <th className="text-center red">Deaths</th>
                                    <th className="text-center serious">Serious Case</th>
                                </tr>
                            </thead>
                            <tbody>
                            {getCountryData.map((country,i) =>  
                                <CountryData key={i} country={country} />)}
                            </tbody>
                        </Table>
                        <BackToTop/>
                    </div>
                </div>)
            : <PreLoader/> } */}
            </>  
        )
    }
}

export default CountryWise
